import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {AppSettings} from '../../app/app.settings';
import { Storage } from '@ionic/storage';
import { Events } from 'ionic-angular';

import { App } from 'ionic-angular';

import { LoginPage } from '../../pages/login/login';
/*
  Generated class for the AuthenticationProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class AuthenticationProvider {

  constructor(public http: HttpClient, public storage: Storage, private events: Events, public app: App) {
  }
  
  

  
    public login(data){
        return new Promise((resolve, reject) => {
            this.http.post(AppSettings.apiUrl + "/auth/login", data).subscribe((res) => {
                    this.storage.set("token", res["token"]).then(() => {
                        this.events.publish("user:loggedin");  
                    });         
                    resolve(res["token"]);             
              },(e) => {
              //console.log(e);
                  reject(e);
              });          
        })
    }
  
  
  
    public register(data){
        return new Promise((resolve, reject) => {
            this.http.post(AppSettings.apiUrl + "/auth/signup", data).subscribe((res) => {
                this.storage.set("token", res["token"]).then(() => {
                    this.events.publish("user:loggedin");  
                }); 
                    
                resolve(res["token"]);
            },(e) => {
                reject(e);
            });          
        })      
    }
    
    public getUserData(){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.get(AppSettings.apiUrl + "/user?token=" + token).subscribe((res) => {
                        this.storage.set("user", res["user"]);
                        this.events.publish("user:retreived", res["user"]);
                        resolve(res["user"]);
                    }, (e) => {
                    //console.log(e);
                        if (e && e.error && e.error.error && e.error.error.status_code === 401){
                                //token expired, logout
                                this.storage.clear();
                                this.app.getRootNav().setRoot(LoginPage);
                                this.app.getRootNav().popToRoot();  
                        }
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }



    public logout(){
        return new Promise((resolve, reject) => {
            
            this.storage.get("token").then((token) => {            
                if (token){
                    this.http.post(AppSettings.apiUrl + "/auth/logout?token=" + token,{}).subscribe((res) => {
                        resolve();
                    }, (e) => {
                        resolve();
                    });   
                }
                else{
                    resolve();
                }
                this.storage.clear();
            });       
        })        
    }  
    
    
    public savePushId(id){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.post(AppSettings.apiUrl + "/auth/updatepushid?token=" + token,{push_id:id}).subscribe((res) => {
                        resolve();                     
                    },(e) => {
                        reject(e);
                    });   
                }
                else{reject();} 
            });                    
        })       
    }            

    public changePassword(email,password,passwordConfirm){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.post(AppSettings.apiUrl + "/auth/reset?token=" + token,{email:email,password:password, password_confirmation:passwordConfirm}).subscribe((res) => {
                        resolve();                     
                    },(e) => {
                        reject(e);
                    });   
                }
                else{reject();} 
            });                    
        })       
    }
    
    public getProfile(userId){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.get(AppSettings.apiUrl + '/profiles/' + userId + '?token=' + token).subscribe((res:any) => {
                        resolve(res.profile);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
    }    
    
    public updateProfile(profile){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.put(AppSettings.apiUrl + "/profiles/" + profile.id + "?token=" + token, profile).subscribe((res:any) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }     


}



