import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events, ModalController,Platform, AlertController} from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { RequestProvider } from '../../providers/request/request';

import { EmailComposer } from '@ionic-native/email-composer';

import { LoginPage } from '../../pages/login/login';

import { EditProfileModal } from '../../modals/edit-profile/edit-profile';
import { ProfileModal } from '../../modals/profile/profile';

/**
 * Generated class for the SettingsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html',
})
export class SettingsPage {
    
    public user:any;
    public client:any;
    public coaches:any;
    
    constructor(public navCtrl: NavController, public navParams: NavParams, public storage: Storage, public events: Events, private emailComposer: EmailComposer, public modalCtrl: ModalController, public plt: Platform, private authProvider: AuthenticationProvider, private alertCtrl: AlertController, private authenticationProvider:AuthenticationProvider, private requestProvider: RequestProvider) {
        
        this.user = {};
        this.storage.get("user").then((user) => {
            if (user){
                this.user = user;
                this.authenticationProvider.getProfile(this.user.id).then((data:any) => {
                    this.client = data;
                });                
            }
        });
        
        this.coaches = {count:0,data:[]};
        this.getCoaches();
        
        this.events.subscribe("request:accepted", () => {
            this.getCoaches();
        })
        
    }
  
    public openRate(){
        if (this.plt.is("ios")){
            window.open("https://itunes.apple.com/", '_system');
        }
        else if (this.plt.is("windows")){
            window.open("https://www.microsoft.com", '_system');
        }
        else{
            window.open("https://play.google.com/", '_system');
        }
        
    }
    public openFollow(){
        window.open("https://www.facebook.com/", '_system');
    }
    
    public openEmail(){
        let email = {
          to: ["taylordonhamling@gmail.com"],
          subject: 'Subject for your support query',
          body: 'Provide details about the support you require.',
          isHtml: true
        };        

        this.emailComposer.open(email);
    }
    
    private getCoaches(){
        this.requestProvider.getCoaches().then((data) => {
            this.coaches = data;
        });
    }
    
    public viewProfile(profile){
        let modal = this.modalCtrl.create(ProfileModal, {profile:profile});       
        modal.present();         
      
    }    
    
    public openEditProfile(){
        let modal = this.modalCtrl.create(EditProfileModal, {profile:this.client}); 
        modal.onDidDismiss(profile => {
            if (profile){
                this.client = profile;
                this.authenticationProvider.updateProfile(this.client);
            }
        })        
        modal.present();         

    }
    
    public removeCoach(coach,index, ev){
        ev.stopPropagation();

        let alert = this.alertCtrl.create({
            title:"Confirm",
            subTitle: "Are you sure you want to remove this coach?",
            message: "They will no longer be able to view or edit your profile or diary.",
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel'
                },
                {
                    text: 'Yes',
                    handler: data => {
                        this.coaches.data.splice(index,1);
                        this.requestProvider.deleteCoach(coach.user_id); 
                   }
                }
            ]
        });
        alert.present();            
    }      
    
    
    public openChangePassword(){
        let alert = this.alertCtrl.create({
            title: 'Change Password',
            inputs: [           
                {
                    name: 'password',
                    placeholder: 'Password',
                    type: 'password'
                },
                {
                    name: 'confirmPassword',
                    placeholder: 'Confirm Password',
                    type: 'password'
                }
            ],
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: data => {
                    }
                },
                {
                    text: 'Change',
                    handler: data => {
                        
                        if (data.password && data.confirmPassword && data.password === data.confirmPassword){

                            
                            this.authProvider.changePassword(this.user.email, data.password, data.confirmPassword).then(() => {
                                let alert = this.alertCtrl.create({
                                    title: 'Success',
                                    message: 'Your password has been updated',
                                    buttons: [
                                        {
                                            text: 'Dismiss',
                                            role: 'cancel',
                                            handler: data => {
                                            }
                                        }                              
                                    ]           
                                }); 
                                alert.present();                                 
                            }).catch(() => {
                                let alert = this.alertCtrl.create({
                                    title: 'Error',
                                    message: 'There was an error while changing your password.',
                                    buttons: [
                                        {
                                            text: 'Cancel',
                                            role: 'cancel',
                                            handler: data => {
                                            }
                                        },
                                         {
                                            text: 'Try Again',
                                            handler: data => {
                                                setTimeout(() => {this.openChangePassword()},200);
                                            }
                                        }                               
                                    ]            
                                });
                                setTimeout(() => {alert.present()}, 200);                                 
                            });
                        }
                        
                        else{
                            let alert = this.alertCtrl.create({
                                title: 'Error',
                                message: 'Passwords do not match',
                                buttons: [
                                    {
                                        text: 'Cancel',
                                        role: 'cancel',
                                        handler: data => {
                                        }
                                    },
                                     {
                                        text: 'Try Again',
                                        handler: data => {
                                            setTimeout(() => {this.openChangePassword()},200);
                                        }
                                    }                               
                                ]            
                            });
                            setTimeout(() => {alert.present()}, 200);                             
                        }
    
                    }
                }
                
            ]
        });
        alert.present();        
    }
    
    public logout(){
        this.authProvider.logout();
        this.navCtrl.setRoot(LoginPage);
        this.navCtrl.popToRoot();             
    }      


}



