import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, ToastController, AlertController, Events } from 'ionic-angular';

import { ExerciseProvider } from '../../providers/exercise/exercise';
import { Storage } from '@ionic/storage';

import { RequestProvider } from '../../providers/request/request';



@Component({
    selector: 'create-message',
    templateUrl: 'create-message.html'
})

export class CreateMessageModal {
    
    public coaches:any;
    public properties: any;
    
    constructor(public platform: Platform, public params: NavParams, public viewCtrl: ViewController, private toastCtrl: ToastController, private exerciseProvider: ExerciseProvider, public storage: Storage, private alertCtrl: AlertController, public events: Events, private requestProvider: RequestProvider) {
        this.properties = {loading:false};
        
        this.coaches = {count:0,data:[]};


        this.getCoaches();
        
    }
    
    
    public getCoaches(){
        this.properties.loading = true;
        this.requestProvider.getCoaches().then((data) => {
            this.properties.loading = false;
            this.coaches = data;
        }).catch(() => {
            this.properties.loading = false;
        });
    }


    
    public selectCoach(coach){
        this.viewCtrl.dismiss(coach);
    }
    
       

    public dismiss() {
        this.viewCtrl.dismiss();
    }
}






