import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {AppSettings} from '../../app/app.settings';
import { Storage } from '@ionic/storage';
import { App } from 'ionic-angular';

import { LoginPage } from '../../pages/login/login';


/*
  Generated class for the AuthenticationProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class DiaryProvider {

  constructor(public http: HttpClient, public storage: Storage, public app: App) {
  }
  

    public createWorkoutExercise(exercise){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.post(AppSettings.apiUrl + "/workoutexercises?token=" + token, exercise).subscribe((res:any) => {
                        resolve(res.workout_exercise);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }
    
    public updateWorkoutExercise(exercise){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.put(AppSettings.apiUrl + "/workoutexercises/" + exercise.id + "?token=" + token, exercise).subscribe((res:any) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }    
    
    
    public deleteWorkoutExercise(id){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.delete(AppSettings.apiUrl + "/workoutexercises/" + id + "?token=" + token).subscribe((res:any) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }      
    
 
    public getWorkout(clientId, date){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.get(AppSettings.apiUrl + '/workouts/' + clientId + '?token=' + token + '&date=' + date + '&client_user_id=' + clientId).subscribe((res:any) => {
                        resolve(res.workouts);
                    }, (e) => {
                        if (e && e.error && e.error.error && e.error.error.status_code === 401){
                                //token expired, logout
                                this.storage.clear();
                                this.app.getRootNav().setRoot(LoginPage);
                                this.app.getRootNav().popToRoot();  
                        }
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
    } 
    

}



