import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {AppSettings} from '../../app/app.settings';
import { Storage } from '@ionic/storage';
import { Events } from 'ionic-angular';

import { App } from 'ionic-angular';
/*
  Generated class for the AuthenticationProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class NotificationProvider {

  constructor(public http: HttpClient, public storage: Storage, private events: Events, public app: App) {
  }

    
    
    public getNotifications(pagination){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.get(AppSettings.apiUrl + "/notifications?token=" + token + "&page=" + pagination.page + "&limit=" + pagination.limit).subscribe((res:any) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }  
    
    public getNotificationCount(){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.get(AppSettings.apiUrl + "/notificationcount?token=" + token).subscribe((res:any) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }     
    
    
    public updateNotification(notification){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.put(AppSettings.apiUrl + "/notifications/" + notification.id + "?token=" + token, notification).subscribe((res:any) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }   
}



