import { Component,ViewChild } from '@angular/core';
import { NavController, NavParams, Events,Content } from 'ionic-angular';

import { MessageProvider } from '../../providers/message/message';

import { Storage } from '@ionic/storage';
import * as moment from 'moment';

@Component({
  selector: 'page-message',
  templateUrl: 'message.html'
})
export class MessagePage {
    @ViewChild(Content) content: Content;
    
    
    public properties:any;
    public conversation:any;
    public user:any;
    public pingInterval: any;
    public message:any;
    
    constructor(public navCtrl: NavController, public params: NavParams, private messageProvider: MessageProvider, public storage: Storage, public events: Events) {
        this.properties = {loading:true};
        this.conversation = {id:this.params.data.conversation.id, participants:this.params.data.conversation.participants ? this.params.data.conversation.participants : []};

        this.getConversation(this.params.data.conversation.id);
        
        this.user = {};
        this.storage.get("user").then((user) => {
            if (user){
                this.user = user;             
            }
        });  
        
        this.pingInterval = setInterval(() => {
            this.pingMessages();
        },20000);  
        
        this.message = "";    
        
        this.events.subscribe("notification:received", (notification) => {
              if (notification.type === "message" && notification.source_id === this.conversation.id){
                  this.pingMessages();
              }              
        });                 
        
    }
    
    ionViewWillLeave() {
      clearInterval(this.pingInterval);
    }     
    
    private getConversation(id){
        this.messageProvider.getConversation(id).then((data) => {
            this.properties.loading = false;
            this.conversation = data;
            this.calculateMessages();
            setTimeout(()=>{this.content.scrollToBottom();},200);
        }).catch(() => {
            this.properties.loading = false;
        })
    }
    
    private pingMessages(){
        if (this.properties.loading){return;}
        this.messageProvider.getConversation(this.conversation.id).then((data:any) => {      
            if (data.messages.length > this.conversation.messages.length){
                this.conversation.participants = data.participants;
                this.conversation.messages = data.messages;
                this.calculateMessages();
                setTimeout(()=>{this.content.scrollToBottom();},200);
            }
        }).catch(() => {});
    }    
    
    public getMessageTime(date){
        return moment(date).fromNow();
    }
    
    public getAvatar(userId){
        let avatar = 'https://api.strengthsheet.com/uploads/profile.jpg';
        for (let participant of this.conversation.participants){
            if (participant.user_id === userId && participant.profile.avatar){
                avatar = participant.profile.avatar;
                break;
            }
        }
        return avatar;
    }


    private calculateMessages(){
        for (var index in this.conversation.messages){

            //check message before is befrom same user
            //check message after is from same user

            let indexInt = parseInt(index);

            let message = this.conversation.messages[indexInt];
            let previousMessage = this.conversation.messages[indexInt - 1];
            let nextMessage = this.conversation.messages[indexInt + 1];


            let isBefore = false;
            let isAfter = false;

            if (previousMessage && message.user_id === previousMessage.user_id){
                isAfter = true;
            }

            if (nextMessage && message.user_id === nextMessage.user_id){
                isBefore = true;
            }

            message.isBefore = isBefore;
            message.isAfter = isAfter;


        }        
    }
    
    
    public openLink(link){
        window.open(link, '_system');
    }
    
    public sendMessage(){
        if (!this.message){return;}
        
        let message = {id:null, conversation_id:this.conversation.id, message: this.message, created: new Date(), user_id: this.user.id};

        this.conversation.messages.push(message);
        
        this.message = "";
        
        this.calculateMessages();
        
        setTimeout(()=>{this.content.scrollToBottom();},200);
        
        this.messageProvider.createConversationMessage(message).then((data) => {
            message.id = data["id"];
            this.events.publish("message:created");
        }).catch(() => {});        
    }

}



