import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {AppSettings} from '../../app/app.settings';
import { Storage } from '@ionic/storage';
import { Events } from 'ionic-angular';

import { App } from 'ionic-angular';

import { LoginPage } from '../../pages/login/login';
/*
  Generated class for the AuthenticationProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class MessageProvider {

  constructor(public http: HttpClient, public storage: Storage, private events: Events, public app: App) {
  }
  
  
    public getConversations(query){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.get(AppSettings.apiUrl + '/conversations?token=' + token + '&search=' + query.search + '&page=' + query.page + '&limit=' + query.limit).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        if (e && e.error && e.error.error && e.error.error.status_code === 401){
                                //token expired, logout
                                this.storage.clear();
                                this.app.getRootNav().setRoot(LoginPage);
                                this.app.getRootNav().popToRoot();  
                        }
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
    }
    
    
    public getConversation(id){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.get(AppSettings.apiUrl + '/conversations/' + id + '?token=' + token).subscribe((res:any) => {
                        resolve(res.conversation);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
    }  
    
    public createConversationMessage(message){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.post(AppSettings.apiUrl + '/conversationmessages?token=' + token, message).subscribe((res:any) => {
                        resolve(res.conversation_message);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
    }
    
    public createConversation(conversation){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.post(AppSettings.apiUrl + '/conversations?token=' + token, conversation).subscribe((res:any) => {
                        resolve(res.conversation);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });         
    }


}



