import { Component,ViewChild } from '@angular/core';
import { NavController, Slides, ModalController, Events, NavParams, AlertController, Platform } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import * as moment from 'moment';
import { DatePickerDirective } from 'ion-datepicker';
import { AddExerciseModal } from '../../modals/add-exercise/add-exercise';
import { EditDiaryExerciseModal } from '../../modals/edit-diary-exercise/edit-diary-exercise';
import { NotificationsModal } from '../../modals/notifications/notifications';
import { DiaryProvider } from '../../providers/diary/diary';
import { NotificationProvider } from '../../providers/notification/notification';

@Component({
  selector: 'page-diary',
  templateUrl: 'diary.html'
})
export class DiaryPage {
    @ViewChild(Slides) slides: Slides;
    @ViewChild(DatePickerDirective) public datepicker: DatePickerDirective;
    
    
    public workoutSlides: any;    
    public selectedDate : Date;
    public workouts: any;
    public user: any;
    private userRetreived: boolean;
    public notificationCount:number;
        
    constructor(public navCtrl: NavController, public params: NavParams, public modalCtrl: ModalController, public storage: Storage, public events: Events, public alertCtrl: AlertController, public plt: Platform, public diaryProvider: DiaryProvider, public notificationProvider: NotificationProvider) {
          this.selectedDate = this.params.data.date ? this.params.data.date : new Date();
          this.setupSlides();
          this.userRetreived = false;
          this.notificationCount = 0;
          this.notificationProvider.getNotificationCount().then((data:string) => {
              this.notificationCount = parseInt(data); 
          });
          
          this.user = {};
          this.storage.get("user").then((user) => {
              if (user){
                  this.user = user;
                  this.userRetreived = true;
                    if (this.workouts.length > 1) {
                        this.getWorkout(this.workouts[7]);
                    }                  
              }
          });
          
          this.events.subscribe("notification:received", (notification) => {
                this.notificationCount +=1;
              
                if (notification.type === "diary"){
                    this.refreshWorkouts();
                }              
          });
          
          this.events.subscribe("notification:viewed", (notification) => {
            this.notificationProvider.getNotificationCount().then((data:string) => {
                this.notificationCount = parseInt(data); 
            });
          });  
          
          
          this.events.subscribe("user:retreived", (user) => {
                if (!this.userRetreived){
                    this.user = user;
                    this.userRetreived = true;
                    this.refreshWorkouts();
                }
          });                   
        
    }
   
    
    public setupSlides(){
        return new Promise((resolve, reject) => {
            this.workoutSlides = [this.selectedDate];
            this.workouts = [{loading:true, retreived:false, exercises: []}];
            
            for (let i = 1; i < 8; i++){
                let newDate = this.calculateDate(this.selectedDate, -i);
                this.workoutSlides.unshift(newDate);
                this.workouts.unshift({loading:true, retreived:false, exercises: []});

                let newDate2 = this.calculateDate(this.selectedDate, i);
                this.workoutSlides.push(newDate2);    
                this.workouts.push({loading:true, retreived:false, exercises: []});   
            }
            resolve();
        })        
    } 
    
    public workoutChanged(){
        this.selectedDate = this.workoutSlides[this.slides.getActiveIndex()];
        this.getWorkout(this.workouts[this.slides.getActiveIndex()]);
        
        if (this.slides.isBeginning()){
            for (let i = 1; i < 8; i++){
                let newDate = this.calculateDate(this.selectedDate, -i);
                this.workoutSlides.unshift(newDate);
                this.workouts.unshift({loading:true, retreived:false, exercises: []});
            } 
            this.slides.slideTo(7, 0, false);          
        }        
        
        else if (this.slides.isEnd()){

            for (let i = 1; i < 8; i++){
                let newDate = this.calculateDate(this.selectedDate, i);
                this.workoutSlides.push(newDate);
                this.workouts.push({loading:true, retreived:false, exercises: []});

            }             
            
        }

    }  
    
    private refreshWorkout(){     
        this.getWorkout(this.workouts[this.slides.getActiveIndex()]);        
    }    
    
    private refreshWorkouts(){
        for (let workout of this.workouts){
            workout.retreived = false;
        }

        this.getWorkout(this.workouts[this.slides.getActiveIndex()]);        
    }
    
    
    public getWorkout(workout) {
        if (workout.retreived || !this.userRetreived){
            return;
        }
        else{
            let formattedDate = moment(this.selectedDate).format('YYYY-MM-DD');
                workout.exercises = [];
                workout.retreived = true;             

            this.diaryProvider.getWorkout(this.user.id, formattedDate).then((data) => {
                let workoutData = data[0];
                
                workout.id = workoutData.id;
                workout.notes = workoutData.notes;
                workout.videos = workoutData.videos;
                workout.exercises = workoutData.exercises;
                workout.loading = false;
                workout.retreived = true;   
                
                if (!workout.exercises){
                    workout.exercises = [];
                }              
                                           
            })
            .catch(()=>{
                workout.loading = false;
                workout.retreived = false;
            });
        }      
        
    }            
    
    
    private calculateDate(date, change){
        return ( d => new Date(d.setDate(d.getDate() + change)) )(new Date(date))
    }
    
    
    public getSelectedDate(){
        return moment(this.selectedDate).calendar(null, {
            sameDay: '[Today]',
            nextDay: '[Tomorrow]',
            nextWeek: 'dddd',
            lastDay: '[Yesterday]',
            lastWeek: '[Last] dddd',
            sameElse: 'dddd, MMMM Do YYYY'
        });
    }
    
    public getSelectedDateKey(){
        return moment(this.selectedDate).format('YYYY-MM-DD');
    }
    
    public getSetString(exercise){
        let returnString = "";
        if (exercise.sets || exercise.reps){
            returnString += (exercise.sets ? exercise.sets : 0) + " set" + (exercise.sets === 1 ? "" : "s") + " of ";
        }        
        if (exercise.reps || exercise.sets){
            returnString += (exercise.reps ? exercise.reps : 0) + " rep" + (exercise.reps === 1 ? "" : "s");
        }

        if (exercise.weight){
            returnString += ", " + exercise.weight + exercise.units;
        }
        if (exercise.percentage){
            returnString += ", " + exercise.percentage + "%";
        }
        if (exercise.rpe){
            returnString += ", @" + exercise.rpe + "RPE";
        }                                
        return returnString.replace(/^,/, '').trim();
    }
    
    
    public changeDay(direction){
        this.selectedDate = this.calculateDate(this.selectedDate, direction);
        this.getWorkout(this.workouts[this.slides.getActiveIndex()]);
        
        if (direction > 0){
            this.slides.slideNext();
        }
        else{
            this.slides.slidePrev();
        }
    }
    
    public changeDate(date){
        this.selectedDate = date;
        this.setupSlides().then(() => {
            this.slides.slideTo(7, 0, false);    
            this.getWorkout(this.workouts[7]);

        });    
    }  
    
    public openEditExerciseModal(exercise, index){
        let modal = this.modalCtrl.create(EditDiaryExerciseModal, {exercise:exercise}); 
        modal.onDidDismiss(updatedExercise => {
            if (updatedExercise && updatedExercise.deleted){
                this.workouts[this.slides.getActiveIndex()].exercises.splice(index,1);
                this.diaryProvider.deleteWorkoutExercise(updatedExercise.id);
                
            }
            else if (updatedExercise){
                Object.assign(exercise, updatedExercise);
                this.diaryProvider.updateWorkoutExercise(exercise);
                
            }
        })        
        modal.present();            
    }     
    
    
    public openAddDiaryModal(){
        let modal = this.modalCtrl.create(AddExerciseModal); 
        modal.onDidDismiss(exercise => {
            if (exercise){
                let currentWorkout = this.workouts[this.slides.getActiveIndex()];
                let workoutExercise = {id:null, client_user_id: this.user.id, date: this.getSelectedDateKey(), order: currentWorkout.exercises.length + 1, exercise_id:exercise.id, exercise:exercise};
                currentWorkout.exercises.push(workoutExercise);
                this.diaryProvider.createWorkoutExercise(workoutExercise).then((data:any) => {
                    currentWorkout.id = data.workout_id;
                    workoutExercise.id = data.id;                    
                });
            }
        })        
        modal.present();            
    }   
    
    public openNotifications(){
        let modal = this.modalCtrl.create(NotificationsModal);         
        modal.present();            
    }       
    
    public openLink(link){
        window.open(link, '_system');
    }

}



