import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, AlertController, Events, LoadingController } from 'ionic-angular';

import { Storage } from '@ionic/storage';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';



import {AppSettings} from '../../app/app.settings';
import { RequestProvider } from '../../providers/request/request';

import * as moment from 'moment';

@Component({
    selector: 'profile',
    templateUrl: 'profile.html'
})

export class ProfileModal {
    public profile:any;
    constructor(public platform: Platform, public params: NavParams, public modalCtrl: ModalController, public loadingCtrl: LoadingController, public viewCtrl: ViewController, private camera: Camera, public storage: Storage, private alertCtrl: AlertController, public events: Events,private transfer: FileTransfer, private requestProvider: RequestProvider) {
        this.profile = this.params.data.profile;
    }


    public dismiss() {
        this.viewCtrl.dismiss();
    }

    public formatDate(date){
        return moment(date).format("dddd, MMMM Do YYYY");
    }

}






