import { Component } from '@angular/core';
import { NavController, Slides, ModalController, Events, NavParams, AlertController, Platform  } from 'ionic-angular';

import { RequestsModal } from '../../modals/requests/requests';
import { CreateMessageModal } from '../../modals/create-message/create-message';
import { MessagePage } from '../message/message';

import { MessageProvider } from '../../providers/message/message';

import { Storage } from '@ionic/storage';
import * as moment from 'moment';

@Component({
  selector: 'page-messages',
  templateUrl: 'messages.html'
})
export class MessagesPage {
    
    public properties:any;
    public conversations:any;
    public user:any;
    
    constructor(public navCtrl: NavController, public params: NavParams, public modalCtrl: ModalController, public storage: Storage, public events: Events, public alertCtrl: AlertController, public plt: Platform, private messageProvider: MessageProvider) {
        
        this.properties = {loading:false, search:"", page:1, limit:99};
        this.conversations = {count:0, data:[]};
        this.getConversations();
        
        this.user = {};
        this.storage.get("user").then((user) => {
            if (user){
                this.user = user;             
            }
        });     
        
        this.events.subscribe("message:created", () => {
            this.getConversations();
        });
        
        
        this.events.subscribe("notification:received", (notification) => {
              if (notification.type === "message"){
                  this.getConversations();
              }              
        });           
        
        
    }
    
    doRefresh(refresher) {
        this.messageProvider.getConversations(this.properties).then((data) => {
            this.conversations = data;
            refresher.complete();
        }).catch(() => {
            refresher.complete();
        });
        
    }     
    
    private getConversations(){
        this.properties.loading = true;
        this.messageProvider.getConversations(this.properties).then((data) => {
            this.properties.loading = false;
            this.conversations = data;
        }).catch(() => {
            this.properties.loading = false;
        }) 
    }
    
    public goToConversation(conversation){
        this.navCtrl.push(MessagePage, {conversation:conversation});
    }
  
  
    public openRequests(){
        let modal = this.modalCtrl.create(RequestsModal);         
        modal.present();            
    } 
    
    public formatDate(date){
        return moment(date).fromNow();
    }   
    
    public openCreateMessage(){
        let modal = this.modalCtrl.create(CreateMessageModal);      
        modal.onDidDismiss(selectedCoach => {
            if (selectedCoach){
                this.messageProvider.createConversation({client_user_id:selectedCoach.user_id}).then((conversation) => {
                    this.goToConversation(conversation);
                    this.getConversations();
                });
            }
        })                   
        modal.present();         
    }      
}



