import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, AlertController, Events, LoadingController } from 'ionic-angular';

import { Storage } from '@ionic/storage';

import { InAppBrowser } from '@ionic-native/in-app-browser';

import { Camera, CameraOptions } from '@ionic-native/camera';

import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';

import {AppSettings} from '../../app/app.settings';

import * as moment from 'moment';

@Component({
    selector: 'edit-diary-exercise',
    templateUrl: 'edit-diary-exercise.html'
})

export class EditDiaryExerciseModal {
    public exercise:any;
    public properties: any;
    constructor(public navCtrl: NavController, public platform: Platform, public params: NavParams, public events: Events, public viewCtrl: ViewController,public storage: Storage, private alertCtrl: AlertController, private iab: InAppBrowser, private camera: Camera,private transfer: FileTransfer, private file: File) {
        this.properties = {};
        
        this.exercise = {};
        Object.assign(this.exercise, this.params.data.exercise);
        this.exercise.rpe = this.exercise.rpe ? this.exercise.rpe : 8;
        this.exercise.rpeScaled = parseFloat(this.exercise.rpe) * 10;    
        
    }
    public changeRpe(amount){
        this.exercise.rpeScaled = this.exercise.rpeScaled + amount;
        if (this.exercise.rpeScaled < 60){
            this.exercise.rpeScaled = 60;
        }
        else if (this.exercise.rpeScaled > 100){
            this.exercise.rpeScaled = 100;
        }
    }
    
    
    private determinePercentage(){
        let reps = this.exercise.reps ? this.exercise.reps : 0;
        let percentages = {0:0,1:100,2:95,3:90,4:88,5:86,6:83,7:80,8:78,9:76,10:75,11:72,12:70,13:66,14:63,15:60};
        let repRounded = Math.floor(reps);
        this.exercise.percentage =  repRounded > 15 ? 50 : percentages[repRounded];;
    }  
    
    public viewVideo(video){
        window.open(video, '_system');
    }  
    
    public uploadVideo(){

        let cameraOptions = {
            sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
            destinationType: this.camera.DestinationType.FILE_URI,
            mediaType: this.camera.MediaType.VIDEO
        }

        this.camera.getPicture(cameraOptions)
            .then((image) => {
              
                
                this.properties.uploadingVideo = true;
                
                const fileTransfer: FileTransferObject = this.transfer.create();                

                let options: FileUploadOptions = {
                    fileKey:"fileToUpload",
                    fileName: this.exercise.id,
                    mimeType : "video/mp4"
                }

               fileTransfer.upload(image, encodeURI(AppSettings.apiUrl + "/upload"), options).then((data) => {
                    this.exercise.video = 'https://api.strengthsheet.com/uploads/' + data.response;              
                    this.properties.uploadingVideo = false;
                }, (err) => {
                    this.properties.uploadingVideo = false;
                    let alert = this.alertCtrl.create({
                        title: "Error",
                        subTitle: "There was a problem uploading your video",
                        message: JSON.stringify(err),
                        buttons: [
                            {
                                text: 'Dismiss',
                                role: 'cancel'
                            }
                        ]
                    });
                    alert.present();                    
                })
      
                
            }, 
            err => {
                //console.log(err)
            });   

    }
    
    public update(){
        this.exercise.rpe = this.exercise.rpeScaled / 10;
        this.viewCtrl.dismiss(this.exercise);
    }
    
    public openDelete(){
        let alert = this.alertCtrl.create({
            title:"Delete Exercise",
            message: "Are you sure you want to delete this exercise?",
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel'
                },
                {
                    text: 'Yes',
                    handler: data => {
                        this.viewCtrl.dismiss({id: this.exercise.id, deleted:true});
                   }
                }
            ]
        });
        alert.present();         
    }

    public dismiss() {
        this.viewCtrl.dismiss();
    }
    

}






