import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {AppSettings} from '../../app/app.settings';
import { Storage } from '@ionic/storage';
import { Events } from 'ionic-angular';
/*
  Generated class for the AuthenticationProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ExerciseProvider {

  constructor(public http: HttpClient, public storage: Storage, private events: Events) {
  }
  
  
    public getExercises(query){
        return new Promise((resolve, reject) => {
            this.storage.get("token").then((token) => {            
                if (token){            
                    this.http.get(AppSettings.apiUrl + '/exercises?token=' + token + '&search=' + query.search + '&page=' + query.page + '&limit=' + query.limit).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
    }
  


}



