import { Component } from '@angular/core';
import { Platform,Events,App, ModalController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { TabsPage } from '../pages/tabs/tabs';
import { LoginPage } from '../pages/login/login';
import { DiaryPage } from '../pages/diary/diary';
import { MessagePage } from '../pages/message/message';
import { RequestsModal } from '../modals/requests/requests';

import { AuthenticationProvider } from '../providers/authentication/authentication';
import { NotificationProvider } from '../providers/notification/notification';

import { OneSignal } from '@ionic-native/onesignal';
import { Storage } from '@ionic/storage';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
    rootPage:any = TabsPage;
  
    public user: any;  

    constructor(public platform: Platform, public app: App, public statusBar: StatusBar, public splashScreen: SplashScreen, public authProvider: AuthenticationProvider, public storage: Storage, private oneSignal: OneSignal, private events:Events, private notificationProvider:NotificationProvider, public modalCtrl: ModalController) {
        this.initializeApp();
        
        this.user = {profile:{}};

        this.storage.get("user").then((user) => {
            if (user){
                this.user = user;
                this.savePushId();
                this.authProvider.getUserData();                       
            }
            else{
                this.app.getRootNav().setRoot(LoginPage);
                this.app.getRootNav().popToRoot();  
            }
            setTimeout(() => {this.splashScreen.hide()},500);
        }).catch(() => {
            this.app.getRootNav().setRoot(LoginPage);
            this.app.getRootNav().popToRoot();           
            this.splashScreen.hide();
           
        });

        
        this.events.subscribe("user:loggedin", () => {
            this.savePushId();
        });        
    }
    
    savePushId(){
        this.oneSignal.getIds().then((data) => {
            if (data.userId){
                //save
                this.authProvider.savePushId(data.userId);
            }
        }).catch((e) => {
            //console.log(e);
        });        
    }    

    initializeApp() {
        this.platform.ready().then(() => {
            // Okay, so the platform is ready and our plugins are available.
            // Here you can do any higher level native things you might need.
            if (this.platform.is("android")) {
                this.statusBar.backgroundColorByHexString("#173e19");
                this.statusBar.styleLightContent();
            }
            else{
                this.statusBar.styleLightContent();
            }

            if (this.platform.is('cordova')){
                

                
                this.oneSignal.startInit("2afedd0c-2e15-421f-8408-89d5975e2957", "623915044954");
                
                this.oneSignal.handleNotificationReceived().subscribe((data) => {
                    data.payload.additionalData["title"] = data.payload.title;
                    let pushData = data.payload.additionalData;
                    this.events.publish("notification:received", pushData); 
                    
                        if (pushData.type === "diary"){
                            let workoutDate = pushData.id.split(":")[1];
                            this.app.getRootNav().push(DiaryPage, {date:workoutDate});
                        }
                        else if (pushData.type === "message"){
                            this.app.getRootNav().push(MessagePage, {conversation:{id:pushData.id}});                           
                        }
                        else if (pushData.type === "request"){
                            let modal = this.modalCtrl.create(RequestsModal);         
                            modal.present();   
                                                        
                        }                      
                          
                });

                
                
                this.oneSignal.handleNotificationOpened().subscribe((data) => {

                    if (!data.notification.isAppInFocus){
                        
                        let pushData = data.notification.payload.additionalData;    
                        
                        if (pushData.type === "diary"){
                            let workoutDate = pushData.id.split(":")[1];
                            this.app.getRootNav().push(DiaryPage, {date:workoutDate});
                        }
                        else if (pushData.type === "message"){
                            this.app.getRootNav().push(MessagePage, {conversation:{id:pushData.id}});                           
                        }
                        else if (pushData.type === "request"){
                            let modal = this.modalCtrl.create(RequestsModal);         
                            modal.present();   
                                                        
                        }      
                        
                        this.notificationProvider.updateNotification({seen: true, id: pushData.notification_id}).then(() => {
                            this.events.publish("notification:viewed");
                        });                   
                    }
                });

                this.oneSignal.endInit();
            }            
            
        });
    }
    
    
    
    
  
}



