import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, AlertController, Events, LoadingController } from 'ionic-angular';

import { Storage } from '@ionic/storage';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';


import { ProfileModal } from '../../modals/profile/profile';
import {AppSettings} from '../../app/app.settings';
import { RequestProvider } from '../../providers/request/request';

import * as moment from 'moment';

@Component({
    selector: 'requests',
    templateUrl: 'requests.html'
})

export class RequestsModal {
    public requests:any;
    public properties:any;
    constructor(public platform: Platform, public params: NavParams, public modalCtrl: ModalController, public loadingCtrl: LoadingController, public viewCtrl: ViewController, private camera: Camera, public storage: Storage, private alertCtrl: AlertController, public events: Events,private transfer: FileTransfer, private requestProvider: RequestProvider) {
        this.properties = {loading:false}
        this.requests = [];
        this.getRequests();
    }
    
    private getRequests(){
        this.properties.loading = true;
        this.requestProvider.getRequests().then((data) => {
            this.properties.loading = false;
            this.requests = data ? data : [];
        }).catch(() => {
            this.properties.loading = false;
        })
    }
    
    
    public viewProfile(profile){
        let modal = this.modalCtrl.create(ProfileModal, {profile:profile});       
        modal.present();         
      
    }


    public acceptRequest(request,index, ev){
        ev.stopPropagation();
        
        let alert = this.alertCtrl.create({
            title:"Confirm",
            subTitle: "Are you sure you want to accept this request?",
            message: "This coach will be able to view and modify your profile and diary once you accept.",
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel'
                },
                {
                    text: 'Yes',
                    handler: data => {
                        let name = request.profile.name;
                        this.requests.splice(index,1);                        
                        this.requestProvider.acceptRequest(request.id).then(() => {
                            this.events.publish("request:accepted");
                            let alert = this.alertCtrl.create({
                                title:"Success",
                                message: name + " is now your coach.",
                                buttons: [
                                    {
                                        text: 'Dismiss',
                                        role: 'cancel'
                                    }
                                ]
                            });
                            alert.present();   
                        });
                   }
                }
            ]
        });
        alert.present();           

    }
    
    public declineRequest(request,index, ev){
        ev.stopPropagation();

        let alert = this.alertCtrl.create({
            title:"Confirm",
            subTitle: "Are you sure you want to decline this request?",
            message: "This coach will be notified and you will not be able to work together.",
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel'
                },
                {
                    text: 'Yes',
                    handler: data => {
                        this.requests.splice(index,1);
                        this.requestProvider.declineRequest(request.id); 
                   }
                }
            ]
        });
        alert.present();           
 
    }    

    public dismiss() {
        this.viewCtrl.dismiss();
    }

    

}






