import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, ToastController, AlertController, Events } from 'ionic-angular';

import { ExerciseProvider } from '../../providers/exercise/exercise';
import { Storage } from '@ionic/storage';

import {ExerciseSearchPipe} from '../../pipes/exercise-search';


@Component({
    selector: 'add-exercise',
    templateUrl: 'add-exercise.html'
})

export class AddExerciseModal {
    
    public exercises:any;
    public recentExercises:any;
    public recommendedExercises:any;
    public searchExercises:any;
    public properties: any;
    public loading:any;
    
    constructor(public platform: Platform, public params: NavParams, public viewCtrl: ViewController, private toastCtrl: ToastController, private exerciseProvider: ExerciseProvider, public storage: Storage, private alertCtrl: AlertController, public events: Events) {
        this.properties = {loading:false, search:"", page:1, limit:50};
        
        this.exercises = {count:0,data:[]};
        this.getExercises();

        
        
    }
    
    
    public getExercises(){
        this.properties.loading = true;
        
        this.exerciseProvider.getExercises(this.properties).then((data) => {
            this.exercises = data;
            this.properties.loading = false;
        }).catch(() => {
            this.properties.loading = false;
        });
    }

    
    public searchStarted(){
        this.properties.page = 1;       
        this.exerciseProvider.getExercises(this.properties).then((data) => {
            this.exercises = data;
        });
    }
    
    public searchCancelled(){
        this.properties.search = '';
        this.properties.page = 1;   
        this.exerciseProvider.getExercises(this.properties).then((data) => {
            this.exercises = data;
        });
    } 
    

    
    public addExercise(exercise){
        this.viewCtrl.dismiss(exercise);
    }
    
       

    public dismiss() {
        this.viewCtrl.dismiss();
    }
}






