import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, AlertController, Events, LoadingController, App } from 'ionic-angular';

import { Storage } from '@ionic/storage';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';

    import { NotificationProvider } from '../../providers/notification/notification';

import {AppSettings} from '../../app/app.settings';

import { DiaryPage } from '../../pages/diary/diary';
import { MessagePage } from '../../pages/message/message';
import { RequestsModal } from '../../modals/requests/requests';

import * as moment from 'moment';

@Component({
    selector: 'notifications',
    templateUrl: 'notifications.html'
})

export class NotificationsModal {
    public notifications:any;
    public properties:any;
    constructor(public platform: Platform, public app:App, public params: NavParams, public nav:NavController, public modalCtrl: ModalController, public loadingCtrl: LoadingController, public viewCtrl: ViewController, private camera: Camera, public storage: Storage, private alertCtrl: AlertController, public events: Events,private transfer: FileTransfer, private notificationProvider: NotificationProvider) {
        this.properties = {loading:false, limit:10, page:1}
        this.notifications = {data:[], count:0};
        this.getNotifications();
        
        this.events.subscribe("notification:received", (notification) => {
            this.properties.page = 1;
            this.getNotifications();
        });        
        
        
        
    }
    
    
    public getNotifications(){
        this.properties.loading = true;
        this.notificationProvider.getNotifications(this.properties).then((data) => {
            this.notifications = data;
            this.properties.loading = false;
        }).catch(() => {
            this.properties.loading = false;
        })
    }
    
    public getMoreNotifications(infiniteScroll){

        this.properties.page = this.properties.page + 1;
        
        this.notificationProvider.getNotifications(this.properties).then((data) => {
            
            for (let item of data["data"]){
                this.notifications.data.push(item);
            }  
                    
            infiniteScroll.complete();
        })
        .catch((e) => {
            infiniteScroll.complete();
        })
    }
    
    
    public openNotification(notification){
        //update as seen in api
        //go to item in app
        
        if (!notification.seen){
            notification.seen = true;
            this.notificationProvider.updateNotification({seen: true, id: notification.id}).then(() => {
                this.events.publish("notification:viewed");
            });        
        }
        
        if (notification.notification_type === "diary"){ 
            let workoutDate = notification.source_id.split(":")[1];
            this.nav.push(DiaryPage, {date:workoutDate});
        }
        else if (notification.notification_type === "message"){       
            this.nav.push(MessagePage, {conversation:{id:notification.source_id}});                 
        }
        else if (notification.notification_type === "request"){            
            let modal = this.modalCtrl.create(RequestsModal);         
            modal.present();   
        } 
        else{
            let alert = this.alertCtrl.create({
              title: 'Notification type: ' + notification.notification_type,
              subTitle: "Created " + this.formatDate(notification.created_at),
              message: "Intiated by " + notification.name,
              buttons: ['Dismiss']
            });
            alert.present();            
        }          
    }
    
    public formatDate(date){
        return moment(date).fromNow();
    }     
    
        
    public dismiss() {
        this.viewCtrl.dismiss();
    }
    
    

}






